.paginado{
    display: flex;
    flex-direction: row;
    padding: 5px;
    background-color: rgb(201, 120, 120);
    /* margin-top: 30%; */
    align-content: space-around;
    justify-content: center;
    border-radius: 20px;
}

.num{
   display: flex;
   justify-content: space-between;
   border-radius: 20px;
   
}


.ul{
    list-style-type: none;
    display: flex;
    justify-content: center;
    gap: 10px;
    color: rgb(24, 24, 24);
    
}
.container {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50% ;
    height: 1.5rem;
    width: 1.5rem;
    border: 1px solid rgb(0, 0, 0);
    margin-bottom: 2rem;
    background-color:rgb(255, 255, 255);
    cursor: pointer;
}